.app__loginform-input {
  color: aliceblue;
  padding: 10px;
  border-radius: 5px;
  background: #0c0c0c;
  border: 1px solid #dcca87;
}

.app__loginform-group {
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
}

.app__loginform-label {
  font-family: var(--font-alt);
  color: var(--color-white);
  font-weight: 400;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  line-height: 22px;
  font-size: 15px;
}

.app__loginform-icon {
  margin-top: -36px;
  margin-bottom: 15px;
  margin-right: 10px;
  color: #fff;
  text-align: right;
}

.app__loginform-button {
  margin-top: 10px;
  display: flex;
  justify-content: center;
}

.app__loginform-button button {
  width: 35%;
  border-radius: 5px;
}

.app__loginform-error {
  font-family: var(--font-alt);
  color: var(--color-golden);
  font-size: 11px;
}
